@import url(https://fonts.googleapis.com/css?family=Lato:300,300i,400,400i,700,700i|Oswald:400,500,700&display=swap);
.header .profile-image {
    text-align: center;
    padding-right: 20px;
}

.header .profile-image img {
    border-radius: 50%;
    max-width: 100px;
}

.header .info {
    padding-left: 20px;
}
.modal-card .modal-card-foot {
    display: flex;
    justify-content: space-between;
}

.is-loading {
    text-align: center;
}
.skills .progress {
    margin-bottom: 5px !important;
}
.skills .progress {
    margin-bottom: 5px !important;
}
.experience-item {
    display: flex;
    margin-bottom: 20px;
}

.experience-item .dates {
    flex: 3 1;
}

.experience-item .explanation {
    flex: 9 1;
}
.education-item {
    display: flex;
    margin-bottom: 20px;
}

.education-item .dates {
    flex: 3 1;
}

.education-item .explanation {
    flex: 9 1;
}
.tag-cloud-container {
    min-height: 300px;
}

.tag-cloud {
    flex: 1 1;
}

.tag-cloud>div {
    -webkit-transition: 1.4s;
    transition: 1.4s;
}
/**
Fonts

font-family: 'Oswald', sans-serif;
font-family: 'Lato', sans-serif;
*/

html {
  background-color: #282c34;
  overflow-y: auto;
  min-height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  background-color: #282c34;
  background: url(/cv/build/static/media/blueprint.7048d16d.jpg) repeat;
  background-size: 200vh;
  /* background: url(./images/header-bg.jpg) repeat; */
  min-height: 100vh;
  padding: 20px;
}

body #root {
  min-height: 100%;
  font-size: 14px;
}

body #root h1,
body #root h2,
body #root h3,
body #root h4,
body #root h5,
body #root h6 {
  font-family: 'Lato', sans-serif;
  margin-bottom: 10px;
  padding-bottom: 10px;
}

body #root h1 {
  font-size: 35px;
  margin: 10px 0;
}

body #root h2 {
  font-size: 20px;
  margin: 10px 0;
}

body #root h3 {
  text-transform: uppercase;
  font-size: 14px;
  text-align: center;
  border-bottom: 1px solid rgba(10, 10, 10, 0.1);
}

body #root .col2 h3 {
  text-align: left;
}

body #root h4 {
  font-size: 14px;
  margin-bottom: 5px;
  padding-bottom: 0;
  font-weight: bold;
}

.columns .column>div:not(:last-child) {
  margin-bottom: 20px;
}

.contact-info .box>div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 6px
}

.contact-info .box>div .icon {
  margin-right: 10px;
}

.cuote {
  font-size: 16px;
  font-style: italic;
}

.cuote_ref {
  font-size: 13px;
  text-align: right;
}
